@import 'mixins';
@import 'variables';

.move {
  @media (min-width: 320px) {
    & {
      @include grid;
      justify-content: center;
    }
    &__content {
      width: 100%;
      grid-column: span 2;
      text-align: left;
      padding-bottom: 32px;
      font-weight: 500;
      font-size: 32px;
      line-height: 32px;
      letter-spacing: -1.5px;
      padding-top: 60px;

      &-txt {
        grid-column: span 2;
        font-weight: 500;
        font-size: 23px;
        line-height: 32.2px;
        max-width: 280px;
      }
    }
  }
  @media (min-width: 576px) {
    & {
      @include grid;
      justify-content: center;
    }

    &__content {
      grid-column: span 3;
      font-weight: 500;
      font-size: 48px;
      line-height: 48px;
      letter-spacing: -1.5px;
      padding-top: 120px;

      &-txt {
        grid-column: span 3;
        font-weight: 500;
        font-size: 23px;
        line-height: 32.2px;
        padding-top: 120px;
      }
    }
  }

  @media (min-width: 1200px) {
    & {
      @include grid;
    }

    &__content {
      grid-column: span 4;
      font-weight: 500;
      font-size: 64px;
      line-height: 48px;
      letter-spacing: -1.5px;

      &-txt {
        grid-column: 6 / 12;
        font-weight: 500;
        font-size: 23px;
        line-height: 32.2px;
        max-width: 100%;
      }
    }
  }

}
